@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.container {
  margin-top: 25px;

  padding: 40px 30px;
  background-color: v.$primary;
  color: white;
  border-radius: 10px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  .header {
    font-size: 30px;
    font-weight: 600;
  }

  .search {
    position: relative;

    input {
      max-width: 100%;
      min-width: 350px;
      height: 70px;
      padding: 15px 15px 15px 5px;
      margin: 0;
      border-radius: 8px;

      &:focus ~ label,
      &:valid ~ label {
        top: -28px;
        right: 0;
        @include f.rtl(right, 0, initial);
        @include f.rtl(left, initial, 0);
        color: #fff;
      }
    }

    label {
      position: absolute;
      top: 23px;
      @include f.rtl(right, 20px, initial);
      @include f.rtl(left, initial, 20px);
      color: black;

      font-size: 20px;

      transition: v.$transition;
    }
    svg {
      position: absolute;
      top: 8px;
      @include f.rtl(left, 20px, initial);
      @include f.rtl(right, initial, 20px);
      color: black;

      cursor: pointer;
    }
  }
}

@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    .search {
      width: 100%;
      margin-top: 25px;
      input {
        width: 100%;
        min-width: unset;
      }
    }
  }
}
