@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.card {
  margin-top: 25px;

  padding: 25px;
  background: #f5f8ff 0% 0% no-repeat padding-box;
  border-radius: 10px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 30px;
  .card-image {
  
    img {
      width: 274px;
      height: 274px;
      object-fit: cover;
      aspect-ratio: 1;
      border-radius: 10px;
    }
  }
  .card-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    gap: 15px 0;

    align-self: flex-start;


    .card-info-header {
      display: flex;
      gap: 0 5px;
      color: v.$secondary;
      font-size: 30px;
      font-weight: 600;


      .rate-stars{
        font-size: 30px;
      }
    } 
    .card-info-underline{
        text-decoration: underline;
        color: #006DFF;

    }

  }
  .card-action {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    align-self: flex-start;

    gap: 40px 0;

    .card-rating {
      display: flex;
      flex-direction: row;
      gap: 0 8px;
      align-items: center;
      


      .rating-text {
        display: flex;
        flex-direction: column;
        span{
          opacity: 0.5;
        }
      }
      .rating-number {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: v.$secondary;
        color: #fff;
        width: 42px;
        height: 42px;
      }
    }

    .button-container {
    
      button {
        display: inline-block;
        padding: 8px 30px;
        background-color: v.$primary;
        border: none;
        color: white;
        text-align: center;
        text-decoration: none;
        border-radius: 5px;

        margin-top: 7px;

        transition: v.$transition;

        min-width: 155px;
        &:hover {
          background-color: v.$secondary;
          border: none;
          color: white;
          transform: scale(0.9);
        }
      }
    }
  }
}






@media screen and (max-width:770px) {
  .card {
    flex-direction: column;
    .card-image {
      width: 100%;
      img {
        width: 100%;
      }

    }
    .card-info {
      .card-info-header {
        .rate-stars {
          font-size: 20px;
          font-weight: 600;
        }
      }
    }
    .card-action {
      width: 100%;
      .card-rating {
        align-self: baseline;
        margin-top: 10px;

      }
      .button-container{
        width: 100%;
        button {
          width: 100% !important;
        }
      }
    }
  }
}